:root {
	--orange: #ff9e5f;
	--orangeGradient: radial-gradient(
		circle at 50% 50%,
		#e68f55,
		#ea9156,
		#ed9358,
		#f19559,
		#f4985b,
		#f89a5c,
		#fb9c5e,
		#ff9e5f
	);
	--green: #82b334;
	--greenGradient: linear-gradient(
		90deg,
		#82b334 0%,
		#91c63c 50%,
		#82b334 100%
	);

	--red: #ff8b94;
	--redDark: #c95b66;
	--redTransparent: #c95b663e;
	--redGradient: linear-gradient(
		90deg,
		#ff8b94 0%,
		rgba(230, 126, 135, 1) 50%,
		rgba(255, 139, 148, 1) 100%
	);
	--blue: #64b5f6;
	--blueGradient: radial-gradient(circle at 40% 50%, #5aa1db, #64b5f6);
	--darkBlue: #0984e3;
	--liliowy: #a29bfe;
	--mietowy: #55efc4;
	--brzuskwiniowy: #fdcb6e;
	--turkusowyDark: #00cec9;
	--turkusowyLight: #81ecec;

	--widthContent: 1200px;

	--shadowText: 1px 1px 1px #000;
}

/* https://flatuicolors.com/palette/us */

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html {
	font-size: 16px;
	font-family: "Lato", arial, sans-serif;
}

strong {
	font-weight: normal;
}

.heading {
	width: 100%;
	font-weight: 900;
	font-size: 100px;
	color: white;
	font-family: "Tangerine", cursive;
	text-shadow: 1px 1px 1px #000;
	text-shadow: var(--shadowText);
}

.app {
}

#nav {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 150px;
	background: var(--blueGradient);
	z-index: 6;
}

#header {
	width: 100%;
	margin-top: 150px;
	margin-bottom: -4px;
}

#page {
	position: relative;
}

#footer {
	position: relative;
	padding-top: 20px;
	background-color: var(--blue);
}

.footer {
	width: var(--widthContent);
	margin: 0 auto;
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
}

.footer::before,
.policy::before {
	content: "";
	background-size: 14px 100%;
	height: 16px;
	width: 100%;
	position: absolute;
	left: 0;
	right: 0;
	bottom: 100%;
	background-image: linear-gradient(315deg, var(--blue) 25%, transparent 25%),
		linear-gradient(45deg, var(--blue) 25%, transparent 25%);
}

/* LOGO */

.footer-logo {
	height: 100%;
	width: 250px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
}

.footer-logo img {
	width: 150px;
}

.phone-in-footer a {
	display: flex;
	text-decoration: none;
	color: white;
	font-style: italic;
	font-family: "Tangerine", cursive;
	transition: all 0.2s ease-out;
}

.phone-in-footer a:hover {
	color: var(--darkBlue);
}

.phone-in-footer a img {
	width: 26px;
}

.phone-in-footer a strong {
	margin-left: 10px;
	font-size: 50px;
	font-weight: 700;
}

/* CONTENT */

.footer-content {
	width: 50%;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: flex-end;
}

.socials a img {
	width: 50px;
	transition: -webkit-transform 0.3s ease-out;
	transition: transform 0.3s ease-out;
	transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
	cursor: pointer;
}

.socials a img:hover {
	-webkit-transform: rotateY(-360deg);
	        transform: rotateY(-360deg);
}

.footer-nav {
	width: 100%;
	display: flex;
	justify-content: space-between;
}

.footer-nav-item {
	list-style: none;
}

.footer-nav-item a {
	color: white;
	text-transform: uppercase;
	text-decoration: none;
	font-weight: 900;
	letter-spacing: 1px;
	font-size: 22px;
	transition: all 0.2s ease-out;
}

.footer-nav-item a:hover {
	color: var(--darkBlue);
}

.footer-nav-item a.active {
	color: var(--darkBlue);
}

.policy {
	margin-top: 20px;
	background-color: var(--darkBlue);
	position: relative;
}

.policy::before {
	background-image: linear-gradient(
			315deg,
			var(--darkBlue) 25%,
			transparent 25%
		),
		linear-gradient(45deg, var(--darkBlue) 25%, transparent 25%);
}

.wrapper-policy {
	width: var(--widthContent);
	margin: 0 auto;
	display: flex;
	justify-content: flex-end;
}

.policy p {
	height: 100px;
	margin-top: 10px;
	font-size: 13px;
	font-weight: 300;
	color: white;
}
.policy p:nth-child(2) {
	margin-left: 5px;
}

.policy p a {
	color: white;
	text-decoration: none;
}

.nav-content {
	display: flex;
	width: var(--widthContent);
	height: 100%;
	margin: 0 auto;
	justify-content: space-between;
	align-items: center;
	overflow: hidden;
}

/* NAVIGATION */

.nav-content ul {
	align-self: flex-end;
	display: flex;
	justify-self: center;
}

.nav-item {
	display: flex;
	height: 100%;
	background-color: orange;
	list-style: none;
	-webkit-transform: translateY(30px);
	        transform: translateY(30px);
}

/* .nav-content ul li:nth-child(even) {
    background: yellow;
} */

.nav-item a {
	display: block;
	height: 100%;
	padding: 40px 20px;
	font-size: 22px;
	font-weight: 900;
	text-decoration: none;
	text-align: center;
	color: white;
	text-shadow: 0px 0px 1px rgb(0, 0, 0);
	background-color: var(--orange);

	box-sizing: content-box;
	border-bottom: 20px solid transparent;
	transition: -webkit-transform 0.2s ease-out;
	transition: transform 0.2s ease-out;
	transition: transform 0.2s ease-out, -webkit-transform 0.2s ease-out;
}

/* SETTINGS FOR LIST ITEMS */

.nav-item a.active {
	-webkit-transform: translateY(-20px);
	        transform: translateY(-20px);
}

.nav-item:nth-child(2) a,
.nav-item:nth-child(5) a {
	background-color: var(--red);
}

.nav-item a:hover {
	-webkit-transform: translateY(-20px);
	        transform: translateY(-20px);
	background-color: var(--orange);
}
.nav-item:nth-child(2) a:hover,
.nav-item:nth-child(5) a:hover {
	background-color: var(--red);
}

.nav-item:nth-child(3) a {
	background-color: var(--green);
}

.nav-item:nth-child(3) a:hover {
	background-color: var(--green);
}

.nav-item:nth-child(4) a {
	background-color: var(--orange);
}

.nav-item:nth-child(4) a:hover {
	background-color: var(--orange);
}

/* LOGO */

.logo {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	height: 80%;
}

