@-webkit-keyframes rotate {
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}

	50% {
		-webkit-transform: rotate(180deg);
		        transform: rotate(180deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes rotate {
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}

	50% {
		-webkit-transform: rotate(180deg);
		        transform: rotate(180deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@-webkit-keyframes fly4 {
	0% {
		-webkit-transform: translateY(0) rotate(0deg);
		        transform: translateY(0) rotate(0deg);
	}
	100% {
		-webkit-transform: translateY(-400px) rotate(360deg);
		        transform: translateY(-400px) rotate(360deg);
	}
}

@keyframes fly4 {
	0% {
		-webkit-transform: translateY(0) rotate(0deg);
		        transform: translateY(0) rotate(0deg);
	}
	100% {
		-webkit-transform: translateY(-400px) rotate(360deg);
		        transform: translateY(-400px) rotate(360deg);
	}
}
@-webkit-keyframes flyDown {
	0% {
		-webkit-transform: translateY(0) rotate(0deg);
		        transform: translateY(0) rotate(0deg);
	}
	100% {
		-webkit-transform: translateY(600px) rotate(360deg);
		        transform: translateY(600px) rotate(360deg);
	}
}
@keyframes flyDown {
	0% {
		-webkit-transform: translateY(0) rotate(0deg);
		        transform: translateY(0) rotate(0deg);
	}
	100% {
		-webkit-transform: translateY(600px) rotate(360deg);
		        transform: translateY(600px) rotate(360deg);
	}
}

@-webkit-keyframes circle {
	0% {
		-webkit-transform: rotate(0deg) translate(-30px) rotate(360deg);
		        transform: rotate(0deg) translate(-30px) rotate(360deg);
	}
	100% {
		-webkit-transform: rotate(360deg) translate(-30px) rotate(-360deg);
		        transform: rotate(360deg) translate(-30px) rotate(-360deg);
	}
}

@keyframes circle {
	0% {
		-webkit-transform: rotate(0deg) translate(-30px) rotate(360deg);
		        transform: rotate(0deg) translate(-30px) rotate(360deg);
	}
	100% {
		-webkit-transform: rotate(360deg) translate(-30px) rotate(-360deg);
		        transform: rotate(360deg) translate(-30px) rotate(-360deg);
	}
}

.page-animations {
	width: 100%;
	background: var(--redGradient);
	padding: 50px 0 100px 0;
	position: relative;
}

.animations {
	width: var(--widthContent);
	margin: 0 auto;
	display: flex;
	justify-content: space-around;
	flex-wrap: wrap;
}

.animations h2 {
	width: 100%;
	margin-bottom: 50px;
	font-weight: 900;
	font-size: 100px;
	color: white;
	font-family: "Tangerine", cursive;
	text-shadow: var(--shadowText);
}

.animations p {
	margin-top: 30px;
	color: white;
	font-size: 14px;
	text-align: center;
	line-height: 150%;
}

.animations p span {
	display: block;
	font-size: 16px;
}

.content-animation-box {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	position: relative;
	width: 30%;
	height: 500px;
	margin-bottom: 40px;
	padding: 20px;
	text-align: center;
	color: white;
	position: relative;
	z-index: 5;
}

.card {
	display: flex;
	flex-direction: column;
	z-index: 2;
	box-shadow: -20px -20px 0px 0px var(--redTransparent);

	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	padding: 20px;
	border-radius: 10px;

	-webkit-clip-path: circle(5% at 90% 90%);

	        clip-path: circle(5% at 90% 90%);
	/* opacity: 0; */

	transition: all 0.3s ease-in-out;
}

.card.card-show {
	-webkit-clip-path: circle(100%);
	        clip-path: circle(100%);
	/* opacity: 1; */
	background-color: #fff;
}

@-webkit-keyframes show {
	0% {
		-webkit-clip-path: circle(5% at 90% 90%);
		        clip-path: circle(5% at 90% 90%);
	}
	100% {
		-webkit-clip-path: circle(100%);
		        clip-path: circle(100%);
	}
}

@keyframes show {
	0% {
		-webkit-clip-path: circle(5% at 90% 90%);
		        clip-path: circle(5% at 90% 90%);
	}
	100% {
		-webkit-clip-path: circle(100%);
		        clip-path: circle(100%);
	}
}
.animation-img-wrapper {
